/* Estilos para el fondo */
.login-background {
  background-image: url('/public/ueprios.jpg'); /* Asegúrate de que esté en /public */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.login-background::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(5px); /* Aplica el desenfoque */
  background-color: rgba(42, 41, 41, 0.382); /* Capa para mayor contraste */
  z-index: 0;
}

.login-background > * {
  position: relative;
  z-index: 1;
}

/* Estilo del contenedor Flex */
.login-flex {
  height: 100vh;
  justify-content: center;
  align-items: center;
}

/* Estilos para el Card */
.login-card {
  width: 400px;
  background-color: rgba(0, 0, 0, 0.2); /* Fondo con transparencia */
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5); /* Sombra suave */
  backdrop-filter: blur(2px); /* Difumina el fondo */
}

/* Estilo para el logo */
.logo-container {
  display: flex;
  justify-content: center;
  margin-bottom: 24px;
}

.logo {
  width: 160px;
}

/* Estilos para los labels */
.ant-form-item-label {
  color: white;
  font-weight: bold;
  font-size: 20px; /* Tamaño del texto del label */
}

/* Estilo de los inputs */
.ant-input, .ant-input-password {
  font-size: 1.25rem; /* Aumento del tamaño del texto dentro de los inputs */
  padding: 12px; /* Aumento del tamaño del padding */
}

/* Estilo del botón */
.ant-btn {
  font-size: 1.25rem; /* Tamaño de la fuente del botón */
  font-weight: bold;  /* Texto en negrita */
  padding: 25px; /* Aumento del tamaño del padding del botón */
  margin-top: 15px;
}

/* Media query para pantallas pequeñas (dispositivos móviles) */
@media (max-width: 768px) {
  .login-background {
    background-size: contain; /* Ajusta el fondo para pantallas pequeñas */
  }

  .login-background > * {
    width: 90%; /* Ajusta el tamaño de los elementos */
    padding: 10px;
  }

  .ant-form-item-label {
    font-size: 1.1rem; /* Ajusta el tamaño de los labels */
  }

  .ant-form-item {
    font-size: 1rem; /* Ajuste en el tamaño del contenido */
  }

  .ant-input, .ant-input-password {
    font-size: 1.125rem; /* Ajuste en el tamaño de los inputs */
  }

  .ant-btn {
    font-size: 1.125rem; /* Ajuste en el tamaño del botón */
  }
}
